@import url("https://fonts.googleapis.com/css?family=Philosopher");
@import url("https://fonts.googleapis.com/css?family=Dancing+Script");
@import url("https://fonts.googleapis.com/css?family=ZCOOL+XiaoWei");
@import url("https://fonts.googleapis.com/css?family=Merriweather|Sawarabi+Mincho|ZCOOL+XiaoWei");
html {
    overflow-x: hidden;
}

@media only screen and (max-width: 350) {
    html {
        overflow-x: hidden;
    }
}

#root {
    max-width: 100%;
}

.particles {
    position: fixed;
    right: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
}

.header-color {
    background: #141e30;
    /* fallback for old browsers */
    background: -webkit-linear-gradient( to right, #243b55, #141e30);
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient( to right, #243b55, #141e30);
    /* fallback for old browsers */
    opacity: 0.6;
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.header-color :hover {
    color: yellow;
}

.menu_btn {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.home-page:before {
    content: " ";
    display: block;
    position: absolute;
    background-color: #141e30;
    background-repeat: no-repeat;
    background-size: cover;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    background-position: 50% 0;
    z-index: -1;
}

.profile-img {
    height: 150px;
    width: 150px;
    border-radius: 50%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
    box-shadow: 5px 5px 5px lightyellow;
}

.name-css {
    font-family: "Dancing Script", cursive;
    font-size: 4rem;
    font-weight: 100;
    color: white;
    text-align: center;
}

.surname {
    color: yellow;
}

.sub-detail {
    font-family: "Dancing Script", cursive;
    color: white;
    font-size: 20px;
    font-weight: 20;
    text-align: center;
    margin-top: -25px;
    letter-spacing: 2px;
}

.sub-detail2 {
    font-family: "Dancing Script", cursive;
    color: white;
    font-size: 26px;
    font-weight: 20;
    text-align: center;
    margin-top: -25px;
    letter-spacing: 2px;
}

.listposition {
    float: right;
}


/* ************
css for About component 
************** */

.about-css {
    margin-top: 200px;
    width: 100%;
}

.head-about {
    text-align: center;
    font-family: "Dancing Script", cursive;
    font-size: 50px;
    color: white;
}

.content-about {
    display: block;
    margin-left: auto;
    margin-right: auto;
    height: auto;
    width: 1000px;
}

@media only screen and (max-width: 1280px) {
    .content-about {
        width: auto;
    }
}

.about-detail1 {
    padding: 20px;
    opacity: 1;
    font-size: 25px;
    font-family: "ZCOOL XiaoWei", serif;
}

.about-detail {
    padding: 20px;
    opacity: 0.9;
    font-size: 20px;
    font-family: "ZCOOL XiaoWei", serif;
}

.about-sub-detail {
    font-size: 15px;
    opacity: 0.8;
    font-family: "Merriweather", serif;
}


/* **********
css for eduction component 
******** */

.education-detail {
    padding: 20px;
    font-size: 25px;
    font-family: "ZCOOL XiaoWei", serif;
}

.bach-course {
    opacity: 0.8;
    font-size: 20px;
}

.bach-subject {
    opacity: 0.8;
    font-family: "Merriweather", serif;
    font-size: 15px;
}

.bach-date {
    float: right;
    opacity: 0.8;
}

.jik {
    margin-left: 70px;
}

p i {
    padding: 20px;
    opacity: 0.8;
}

p i:first-child {
    color: lightcoral;
}

p i:nth-child(2) {
    color: lightseagreen;
}

p i:nth-child(3) {
    color: yellow;
}

p i:nth-child(4) {
    color: lightskyblue;
}

p i:nth-child(6) {
    color: lightgreen;
}

p i:nth-child(5) {
    color: lightsalmon;
}

ul li {
    padding: 10px;
    font-family: "ZCOOL XiaoWei", serif;
    font-size: 19px;
}

.achievements {
    padding: 20px;
    font-size: 19px;
}


