/* about css again */

.bg_color {
    background: #141e30;
    /* fallback for old browsers */
    background: -webkit-linear-gradient( to right, #243b55, #141e30);
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient( to right, #243b55, #141e30);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    display: block;
    position: absolute;
    background-repeat: no-repeat;
    background-size: cover;
    left: 0;
    width: 100%;
    background-position: 100% 0;
}

.expbg_color {
    background-color: lightyellow;
}

.about_row {
    display: flex;
    flex-direction: row;
    margin-top: 30%;
    margin-bottom: 20%;
}

.profile-pic {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 80%;
    height: 100%;
}

.contact-about {
    display: flex;
    flex-direction: row;
}

@media only screen and (max-width: 800px) {
    .about_row {
        flex-direction: column;
        margin-top: 75%;
    }
    .profile-pic {
        width: 100%;
        height: auto;
    }
    .new_about {
        text-align: center;
    }
    .contact-about {
        display: flex;
        flex-direction: column;
    }
}

.exp_heading {
    text-align: center;
    font-size: 40px;
    color: #cac531;
}

.workchips {
    display: flex;
    margin-top: 10%;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: wrap;
    grid-row-gap: 1em;
}

.workcard {
    color: white;
    width: 345px;
    background: #2c3e50;
    /* fallback for old browsers */
    background: -webkit-linear-gradient( to bottom, #4ca1af, #2c3e50);
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient( to bottom, #4ca1af, #2c3e50);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

@media only screen and (max-width: 800px) {
    .workchips {
        display: grid;
        align-items: center;
        justify-content: center;
        width: 100%;
        column-gap: 1rem;
        grid-row-gap: 2em;
    }
}

@media (min-width: 800px) and (max-width: 1080px) {
    .workchips {
        display: flex;
        justify-content: space-evenly;
        flex-wrap: wrap;
    }
}

.edu_row {
    width: 100%;
